import React, { Component } from 'react';
const _ = require('lodash');

class CardTable extends Component {

  constructor(props) {
    super(props);

    this.state = {
      playerChange: false,
      bankerChange: false,
      dPlayerChange: false,
      dBankerChange: false,
      tieChange: false,
      totalChange: false
    }
  }

  componentWillReceiveProps(nextProps) {
    let history = _.get(this.props.data, 'm_RoadHisCnt')
    //let mNow = _.get(this.props.data, 'm_now')

    let nextHistory = _.get(nextProps.data, 'm_RoadHisCnt')
    //let nextNow = _.get(nextProps.data, 'm_now')

    this.setState({
      playerChange: _.get(history, 'Player', 0) !== _.get(nextHistory, 'Player', 0),
      bankerChange: _.get(history, 'Banker', 0) !== _.get(nextHistory, 'Banker', 0),
      dBankerChange: _.get(history, 'DBanker', 0) !== _.get(nextHistory, 'DBanker', 0),
      dPlayerChange: _.get(history, 'DPlayer', 0) !== _.get(nextHistory, 'DPlayer', 0),
      tieChange: _.get(history, 'Tie', 0) !== _.get(nextHistory, 'Tie', 0),
      totalChange: _.get(history, 'HistoryCnt', 0) !== _.get(nextHistory, 'HistoryCnt', 0),
    })
  }

  checkStyle(checkCase) {
    let changeColor = { color: '#f00' }
    let normalColor = { color: '#212529' }
    switch (checkCase) {
      case 'Banker':
        return this.state.bankerChange ? changeColor : normalColor
      case 'Player':
        return this.state.playerChange ? changeColor : normalColor
      case 'DPlayer':
        return this.state.dPlayerChange ? changeColor : normalColor
      case 'DBanker':
        return this.state.dBankerChange ? changeColor : normalColor
      case 'Tie':
        return this.state.tieChange ? changeColor : normalColor
      case 'Total':
        return this.state.totalChange ? changeColor : normalColor
      default:
        return normalColor
    }

  }
  
  render() {
    let history = _.get(this.props.data, 'm_RoadHisCnt')
    let mNow = _.get(this.props.data, 'm_now')
    
    //console.log(history)
    //console.log(mNow)
    //console.log(this.state)

    return (
      <div className="card">
      <h4 className="card-header">{_.get(mNow, 'did', 'Untitled')}</h4>
      <div className="card-body">
      
          <table className="table table-striped">
            <tbody>
              <tr>
                <th scope="row">B</th>
                <td style={this.checkStyle('Banker')}>{_.get(history, 'Banker', 0)}</td>
                <th scope="row">P</th>
                <td style={this.checkStyle('Player')}> {_.get(history, 'Player', 0)}</td>
                <th scope="row">T</th>
                <td style={this.checkStyle('Tie')}>{_.get(history, 'Tie', 0)}</td>
              </tr>
              <tr>
                <th scope="row">DB</th>
                <td style={this.checkStyle('DBanker')}>{_.get(history, 'DBanker', 0)}</td>
                <th scope="row">DP</th>
                <td style={this.checkStyle('DPlayer')}>{_.get(history, 'DPlayer', 0)}</td>
                <th scope="row">Total</th>
                <td style={this.checkStyle('Total')}>{_.get(history, 'HistoryCnt', 0)}</td>
              </tr>
            </tbody>
          </table>

      </div>
      </div>
    )
  }
  
}

export default CardTable


