import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import CardTable from './components/CardTable';

const _ = require('lodash');
const rws = require('robust-websocket');

class App extends Component {
  
  constructor(props) {
    super(props);
    
    this.state = {
      A: null,
      B: null,
      C: null,
      D: null,
      E: null,
      F: null,
      H: null,
      I: null,
      J: null,
    }
    
    this.values = []
  }
  
  componentDidMount() {
    this.initSocket()
  }
  
  initSocket() {
    const ws = 
    new rws('wss://mobileroad.bacc6666.com/', null, {
    shouldReconnect: (event, wss) => {
      return Math.pow(1.5, ws.attempts) * 500
    }
  });
  
  ws.addEventListener('open', this.openedSocket);
  
  ws.addEventListener('message', this.receivedMessage);
  
  ws.addEventListener('close', this.closedSocket);
}

openedSocket = (event) => {
  //console.log('__OPEN__')
}

receivedMessage = (event) => {
  //console.log(event.data)
  let parseJson = JSON.parse(event.data)
  if (parseJson.m_now.tp === 'Bacc') {

    let history = parseJson.m_RoadHisCnt
    let mNow = parseJson.m_now
    
    switch (mNow.did) {
      case 'A':
      this.setState({
        A: parseJson
      })
      break
      case 'B':
      this.setState({
        B: parseJson
      })
      break
      case 'C':
      this.setState({
        C: parseJson
      })
      break
      case 'D':
      this.setState({
        D: parseJson
      })
      break
      case 'E':
      this.setState({
        E: parseJson
      })
      break
      case 'F':
      this.setState({
        F: parseJson
      })
      break
      case 'H':
      this.setState({
        H: parseJson
      })
      break
      case 'I':
      this.setState({
        I: parseJson
      })
      break
      case 'J':
      this.setState({
        J: parseJson
      })
      break
      default: break;
    }
    // if(mNow.did === 'A') {
    //   this.setState({
    //     A: parseJson
    //   })
    // }
    //console.log(history)
    //console.log(mNow)
    console.log('Table: ', mNow.did, '\nTm: ', mNow.tm,'\nSt: ', mNow.st, '\nWin:', _.get(mNow, 'win', '-'))
    console.log('Banker: ', history.Banker, '\nPlayer: ', history.Player, '\nDBanker: ', history.DBanker, '\nDPlayer: ', history.DPlayer, '\nTie: ', history.Tie, '\nHistoryCount: ', history.HistoryCnt)
    console.log('***************************************')
  }
  //this.values.push(parseJson)
}

closedSocket = (event) => {
  // console.log('__CLOSE__')
  // let onlyBacc = _.filter(this.values, (data) => {
  //   return data.m_now.tp === "Bacc"
  // });
  
  // console.log(onlyBacc);
  // this.values = []
}

render() {
  return (
    
    <div data-reactroot>
    <div className="App">
    
    <div className= "container-fluid">
    
    
    <header className="App-header">
    <img src={logo} className="App-logo" alt="logo" />
    <h1 className="App-title">The Gray Area</h1>
    </header>
    
    {/* A B C */}
    <div className="card-deck mr-3 ml-3 mb-3">
    
    {/* A */}
    <CardTable data={this.state.A} />
    {/* B */}
    <CardTable data={this.state.B} />
    {/* C */}
    <CardTable data={this.state.C} />
    
    </div>
    
    {/* D E F */}
    <div className="card-deck mr-3 ml-3 mb-3">
    
    {/* D */}
    <CardTable data={this.state.D} />
    {/* E */}
    <CardTable data={this.state.E} />
    {/* F */}
    <CardTable data={this.state.F} />
    
    </div>
    
    {/* H I J */}
    <div className="card-deck mr-3 ml-3 mb-3">
    
    {/* H */}
    <CardTable data={this.state.H} />
    {/* I */}
    <CardTable data={this.state.I} />
    {/* J */}
    <CardTable data={this.state.J} />
    
    </div>
    
    </div>
    
    <footer className="blog-footer">
    <p>&copy; tga 2017</p>
    </footer>
    
    </div>
    
    </div>
  );
}
}

export default App;



